<template>
  <v-container id="precisetrain" fluid tag="section">
    <template v-if="flag7 == 0">
      <v-skeleton-loader v-bind="attrs" type="article, actions" height="500px"></v-skeleton-loader>
    </template>

    <template v-else>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <template v-for="n in steps">
            <v-stepper-step :key="`${n}-step`" :complete="e1 > n" :step="n">
              步骤 {{ n }}
            </v-stepper-step>

            <v-divider v-if="n !== steps" :key="n"></v-divider>
          </template>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content v-for="n in steps" :key="`${n}-content`" :step="n">
            <template v-if="n == 1">
              <v-list two-line flat>
                <v-subheader style="font-size: 18px;">请选择题目来源</v-subheader>
                <v-list-item-group v-model="sources_choose" multiple>
                  <v-list-item v-for="item in sources" :key="item">
                    <template v-slot:default="{ active }">
                      <v-list-item-action>
                        <v-checkbox :input-value="active" color="primary"></v-checkbox>
                      </v-list-item-action>
                      <v-list-item-content>
                        <v-list-item-title>{{ item }}</v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </template>

            <template v-if="n == 2">
              <v-list two-line flat>
                <v-subheader style="font-size: 18px;">请选择知识点类型</v-subheader>
                <v-list-item-group v-model="categories_choose" multiple :key="key">
                  <v-list-item v-for="(item, index) in categories" :key="index">
                    <template v-slot:default="{ active }">
                      <v-list-item-action>
                        <v-checkbox :input-value="active" color="primary"></v-checkbox>
                      </v-list-item-action>
                      <v-list-item-content>
                        <v-list-item-title>{{ item }}</v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </template>

            <template v-if="n == 3">
              <v-list two-line flat>
                <v-subheader style="font-size: 18px;">请选择题目难度</v-subheader>
                <v-list-item-group v-model="difficulties_choose">
                  <v-list-item v-for="(item, i) in difficulties" :key="i">
                    <v-list-item-content>
                      <v-list-item-title v-text="item"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </template>

            <v-btn v-if="n == 3" color="primary" @click="jump">
              进入答题
            </v-btn>
            <v-btn v-else color="primary" @click="nextStep(n)">
              下一步
            </v-btn>

            <v-btn v-if="n != 1" @click="backStep(n)" text>返回上一步</v-btn>
          </v-stepper-content>
        </v-stepper-items>

      </v-stepper>
    </template>
  </v-container>
</template>

<script>
  export default {
    name: 'precisetrain',
    data() {
      return {
        e1: 1,
        steps: 3,
        sources: [
          '全部题库', '课本原题', '金考卷', '5年高考3年模拟', '错题本'
        ],
        categories: [],
        difficulties: [
          '简单', '中等', '困难'
        ],
        sources_choose: [],
        categories_choose: [],
        difficulties_choose: '',
        flag7: 1,
        len: 0,
        paper: [],
        key: 0,

      }
    },
    created: function () {
      this.get_all_category()

      // var that = this;
      // this.$axios({
      //   method: 'post',
      //   url: '/api/join_class/',
      //   params: {
      //     id: that.$global.userMsg.id,
      //     class_code: that.change_class,
      //   },
      // })
      // .then(function (response) {
      //   if (response.data.error_num == 0) {
      //     console.log(response.data)
      //     that.$global.userMsg.classes = response.data.classes
      //     that.userMsg.classes = response.data.classes
      //     that.$forceUpdate();
      //     alert("修改成功！")
      //   } else {
      //     alert("修改失败！")
      //   }
      // })
    },

    watch: {
      steps(val) {
        if (this.e1 > val) {
          this.e1 = val
        }
      },
      vertical() {
        this.e1 = 2
        requestAnimationFrame(() => this.e1 = 1) // Workarounds
      },
    },

    methods: {
      get_all_category: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_all_category/',
          params: {}
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.categories = response.data.cates
            setTimeout(() => {
              that.key++
            }, 1000)
          } else if (response.data.error_num == 1) {
            alert("获取类别失败！")
          }
        })
      },
      onInput(val) {
        this.steps = parseInt(val)
      },
      nextStep(n) {
        this.e1 = n + 1
      },
      backStep(n) {
        this.e1 = n - 1
      },
      get: function (i, len) {
        var that = this;
        if (i < len) {
          var q = new Object();
          that.$axios({
            method: 'get',
            url: '/api/get_question/',
            params: {
              _id: that.paper[i]
            }
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              q.id = that.paper[i]
              q.des = response.data.description
              q.qc = response.data.question_class
              if (q.qc == "choice") {
                var choice = new Array();
                choice[0] = response.data.choice[0].A
                choice[1] = response.data.choice[0].B
                choice[2] = response.data.choice[0].C
                choice[3] = response.data.choice[0].D
                q.cho = choice
              }
              q.ans = response.data.answer
              that.$global.paper.push(q)
              i++;
              that.get(i, len)
            } else if (response.data.error_num == 1) {
              console.log(i + 1, "失败！")
            }
          })
        } else if (i == len) {
          console.log(that.$global.paper)
        }
      },
      jump() {
        var that = this;
        this.flag7 = 0;
        let pow = 0;
        for (var i = 0; i < that.categories_choose.length; i++) {
          pow += Math.pow(2, that.categories_choose[i])
        }
        console.log("pow", pow)
        this.$axios({
          method: 'get',
          url: '/api/get_paper_by_category/',
          params: {
            pow: pow,
            difficulty: that.difficulties_choose,
          }
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.$global.paper = response.data.paper
            that.$global.q_id = response.data.q_id
            that.len = response.data.paper.length
            that.$global.len = response.data.paper.length
            that.$global.paper_name = ''
            // that.get(0, that.len)
            that.$router.push({
              path: './precisetrainpage'
            })
            that.flag7 = 1;
          } else if (response.data.error_num == 1) {
            alert("进入答题失败！")
          }
        })
      }
    },
  }
</script>